@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}
.App {
  background-color: #111;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #888;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #444;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.row {
  margin-left: 20px;
  color: white;
}
h2 {
  margin-top: 30px;
}
.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 30px;
}
.row__posters::-webkit-scrollbar {
  display: none;
}
img {
  display: inline-block;
  object-fit: contain;

  max-height: 200px;
  margin-right: 40px;
  transition: transform 450ms;
}
.row__posters img:hover {
  transform: scale(1.08);
  opacity: 1;
}
.row__posterLarge {
  max-height: 250px;
}
.row__posterLarge:hover {
  transform: scale(1.09);
}
a {
  height: 200px;
  margin-right: 8px;
}

.banner {
  color: white;
  object-fit: contain;
  height: 550px;
  margin-bottom: 20px;
}
.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.9rem;
  max-width: 360px;
  height: 80px;
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}

.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.banner--fadeBottom {
  height: 14.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(26, 26, 26, 0.61),
    #111
  );
}

.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 40px;
  padding: 30px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  transition: all 0.5s;
  transition-timing-function: ease-in;
}

.nav__black {
  background-color: #111;
}
.nav__logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}
.nav button {
  position: fixed;
  right: 60px;
  object-fit: contain;
}
.cancel__logo {
  position: fixed;
  right: 70px;
  display: none;
  cursor: pointer;
}

.contact__fill {
  padding: 5rem;
  padding-left: 5rem;
  display: block;
  margin: 0 auto;
  color: white;
  transition: all 0.7s ease-in;
}
form {
  padding: 20px;
  padding-left: 20px;
  border: rgb(94, 180, 219) solid 6px;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    90deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(143, 155, 190, 1) 100%
  );
  text-align: center;
}
.contact__form {
  padding: 200px;
  display: flex;
  justify-content: space-evenly;
  font-family: "Itim", cursive;
}

.contact__image img {
  max-height: 500px;
}
.contact__fill {
  width: 40%;
}
.form-group {
  font-size: 120%;
}
input {
  margin-top: 8px;
  margin-bottom: 8px;
  width: 60%;
  padding: 12px 20px;
  border: 1px solid cadetblue;
  border-radius: 20px;
  box-sizing: border-box;
}
textarea {
  margin-top: 8px;
  margin-bottom: 8px;
  width: 60%;
  padding: 12px 20px;
  border: 1px solid cadetblue;
  border-radius: 20px;
  box-sizing: border-box;
}
.form-group button {
  cursor: pointer;
  margin-top: 4px;
  padding: 9px;
  border: 3px solid rgb(247, 247, 247);
  width: 30%;

  border-radius: 9px;
  transition: all 450ms;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    90deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(143, 155, 190, 1) 100%
  );
}
.form-group button:hover {
  transform: scale(1.05);
  background-color: #4caf50;
  background: linear-gradient(
    90deg,
    rgb(34, 195, 160) 0%,
    rgb(6, 173, 145) 100%
  );
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.movie__details {
  background-color: #111;
  color: white;
}
.review {
  font-family: "Lato", sans-serif;
  display: flex;
  padding: 20px;
  align-items: center;
  justify-content: space-evenly;
}
.rating__no {
  display: inline-block;
  margin-right: 20px;
}
.row__posterNew {
  position: relative;
  top: -120px;
  max-height: 400px;
  border: black solid 0px;
  border-radius: 25%;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.movie__rating {
  max-width: 50%;
}
.movie__rating p {
  font-size: 120%;
  margin-top: 10px;
}
.detail__icon {
  color: white;
  background-color: #222;
  display: flex;
  justify-content: space-evenly;
  padding: 200px;
}
.wrap {
  transition: transform 450ms;
}
.wrap:hover {
  transform: scale(1.09);
}

.see__bottom {
  color: red;
}

.contact {
  font-family: Georgia, serif;
  margin-top: 20px;
  padding: 20px;
  background-color: #222;
  color: white;
}
.contact img {
  display: block !important;
  margin: 0 auto !important;
  height: 60px;
}
.mid__button {
  display: block;
  background-color: rgba(84, 83, 83, 0.5);
  margin: 0 auto !important;
  margin-top: 20px !important;
  margin-bottom: 10px !important;
}
.quick__links {
  margin-left: 2px;
  margin-bottom: 10px;
}
.quick__links h4 {
  margin-bottom: 12px;
}
.quick__links a {
  text-decoration: none;
  color: #0f79af;
}
.quick__links a:hover {
  color: white;
}
.copyright {
  margin-top: 20px !important;
  text-align: center;
  color: #b1b1b1;
}

